.wrap {
  width: 100vw;
  height: 100vh;
  background-color: black;
  overflow: hidden;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

.triggerBtn{
  position: absolute;
  top: var(--spacing-md);
  left: 10px;
  background-color: rgba(255,255,255,0.2);
  border:1px solid rgba(255,255,255,.5);
  color: rgba(255,255,255,.5);
  padding: 10px 20px;
  border-radius: var(--border-radius-soft);
  font-size: calc(var(--font-size-sm)); 
  z-index: 9999;
}

.content {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  gap: 4vw;
  padding: 2vw;
}

.canvasWrap {
  position: relative;
  width: 100%;
  height: 100%;
  flex: 1;
}

.canvasWrap canvas {
  width: 100% !important;
  height: 100% !important;
  display: block;
}

.logo {
  flex:1;
  color: white;
  font-size: calc(var(--font-size-xl) * 2);
  text-align: left;
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: nowrap;
  align-items: center;
  gap: 0.1em;
}

.logo span:nth-child(1) {
  font-weight: var(--font-weight-bold);
}
.logo span:nth-child(2) {
  font-weight: var(--font-weight-light);
}

.rim-light{
  position: absolute;
  width: 350px;
  height: 350px;
  left:50%;
  top:50%;
  transform: scale(1) translate(-50%, -50%);
  border-radius: 50%;
  /* background: radial-gradient(circle, #00ffff 20%, #00ffff40 90%, transparent 80%); */
  background: #883d00;
  /* box-shadow:
    0 0 15px #0011ff,
    0 0 30px #6200ff,
    0 0 50px #ff0000aa;  */
  animation: pulse 3000ms infinite ease-in-out;
  filter: blur(180px);
  pointer-events: none;
}

@keyframes pulse {
  0% {
    transform: scale(1) translate(-50%, -50%);
    opacity: 0.8;
    filter: blur(180px);
  }
  50% {
    transform: scale(1.2) translate(-50%, -50%);
    opacity: 1;
    filter: blur(160px);
  }
  100% {
    transform: scale(1) translate(-50%, -50%);
    opacity: 0.8;
    filter: blur(180px);
  }
}

@media screen and (max-width: 768px) {
  .content {
    flex-direction: column;
    gap:0px;
  }

  .canvasWrap {
    width: 100%;
  }

  .logo {
    text-align: center;
    font-size: 2.5rem;
    position: absolute;
    width: 100%;
    bottom:20%;
  }
}
