@import url("https://cdn.jsdelivr.net/gh/orioncactus/Pretendard/dist/web/static/pretendard.css");

:root {
  /* color */
  --color-primary: #007bff;
  --color-secondary: #6c757d;
  --color-white: #ffffff;
  --color-light: #f8f9fa;
  --color-dark: #181818;
  --color-background: #ffffff;
  --color-text: #181818;
  --color-border: #dfdfdf;
  --color-hover: #f0f0f0;

  /* font */
  font-size: 16px;
  font-family: "Pretendard", sans-serif;
  --font-primary: "Pretendard", sans-serif;
  --font-size-xs: 0.75rem; /* 12px */
  --font-size-sm: 0.875rem; /* 14px */
  --font-size-base: 1rem; /* 16px */
  --font-size-lg: 1.125rem; /* 18px */
  --font-size-xl: 1.5rem; /* 24px */
  --font-size-xxl: 2rem; /* 24px */
  --font-weight-light: 200;
  --font-weight-regular: 400;
  --font-weight-semiBold: 600;
  --font-weight-bold: 700;
  --line-height: 1.5;
  --responsive-font-size: 1rem;

  /* spacing */
  --spacing-xs: 0.25rem; /* 4px */
  --spacing-sm: 0.5rem; /* 8px */
  --spacing-md: 1rem; /* 16px */
  --spacing-lg: 1.5rem; /* 24px */
  --spacing-xl: 2rem; /* 32px */

  /* button style */
  --button-padding: 12px 24px;
  --button-border-radius: 6px;
  --button-font-size: var(--font-size-base);
  --button-hover-opacity: 0.8;

  /* box style */
  --box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  --box-shadow-lg: 0px 4px 6px rgba(0, 0, 0, 0.1);
  --box-shadow-sm: 1px 4px 6px rgba(0, 0, 0, 0.01);
  --border-radius: 0.5rem;
  --border-radius-soft: 0.5rem;
  --border-radius-solid: 0.25rem;
  --border-radius-md: 0.375rem;

  /* responsive breakpoint */
  --breakpoint-xs: 480px;
  --breakpoint-sm: 768px;
  --breakpoint-md: 1024px;
  --breakpoint-lg: 1280px;
  --breakpoint-xl: 1440px;

  /* scrollbar color style */
  --scrollbar-width: 8px;
  --scrollbar-thumb-color: var(--color-border);
  --scrollbar-track-color: var(--color-hover);
  /* --scrollbar-thumb-hover: var(--color-border); */

  /* animation */
  --transition-ease-out-sl: cubic-bezier(0.075, 0.82, 0.165, 1);
  --transition-ease-out-fs: cubic-bezier(0, 1, 0, 1);
}

/* scrollbar style */
::-webkit-scrollbar {
  width: var(--scrollbar-width);
  height: var(--scrollbar-width);
}

::-webkit-scrollbar-track {
  background: var(--scrollbar-track-color);
  border-radius: 10px;
}

::-webkit-scrollbar-thumb {
  background: var(--scrollbar-thumb-color);
  border-radius: 10px;
}

::-webkit-scrollbar-thumb:hover {
  background: var(--scrollbar-thumb-hover);
}

/* scrollbar style only firefox*/
* {
  scrollbar-width: thin;
  scrollbar-color: var(--scrollbar-thumb-color) var(--scrollbar-track-color);
}

/* responsive */
@media (max-width: var(--breakpoint-lg)) {
  :root {
  }
}

@media (max-width: var(--breakpoint-sm)) {
  :root {
    --responsive-font-size: 0.875rem; /* 14px */
    --scrollbar-width: 6px;
  }
}

@media (max-width: var(--breakpoint-xs)) {
  :root {
    --responsive-font-size: 0.8125rem; /* 13px */
    --scrollbar-width: 2px;
  }
}
