.wrap {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  background: radial-gradient(
    circle at 40% 40%,
    var(--color-primary),
    black 100%
  );
  animation: backgroundMove 1s ease-in-out infinite alternate,
    backgroundOpacity 1.5s ease-in-out infinite alternate;
  background-repeat: no-repeat;
  background-size: 150% 150%;
  display: flex;
  align-items: center;
  justify-content: center;
}

@keyframes backgroundMove {
  0% {
    background-position: 0% 0%;
  }
  100% {
    background-position: 100% 100%;
  }
}

@keyframes backgroundOpacity {
  0% {
    background-size: 150% 150%;
  }
  100% {
    background-size: 100% 100%;
  }
}

.wrap::before {
  content: "";
  position: absolute;
  inset: 0;
  background-image: url(../../assets/grain01.svg);
  background-size: cover;
  opacity: 1.1;
  mix-blend-mode: overlay;
  pointer-events: none;
  z-index: 1;
  filter: contrast(190%) brightness(100%);
}

